<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 消息提示信息 -->
      <el-alert title="添加商品信息" type="info" show-icon center> </el-alert>

      <!-- 步骤条区域 -->
      <el-steps :active="+activeIndex" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- tab栏区域 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="changeTabs"
          @tab-click="tabChange"
        >
          <el-tab-pane label="基本信息" name="0"
            >基本信息
            <template>
              <el-form-item label="商品名称" prop="goods_name">
                <el-input v-model="addForm.goods_name"></el-input>
              </el-form-item>
              <el-form-item label="商品价格" prop="goods_price">
                <el-input
                  v-model="addForm.goods_price"
                  type="number"
                ></el-input>
              </el-form-item>
              <el-form-item label="商品数量" prop="goods_number">
                <el-input v-model="addForm.goods_number"></el-input>
              </el-form-item>
              <el-form-item label="商品重量" prop="goods_weight">
                <el-input v-model="addForm.goods_weight"></el-input>
              </el-form-item>
              <el-form-item label="商品分类" prop="goods_cat">
                <!-- 级联选择器 -->
                <el-cascader
                  v-model="addForm.goods_cat"
                  :options="selectList"
                  :props="props"
                  @change="handleChange"
                ></el-cascader>
              </el-form-item>
            </template>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!-- 多选框 -->
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyData"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <!-- 静态属性表单 -->
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- 图片上传 -->
            <el-upload
              class="upload-demo"
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="uploadHeader"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="addForm.goods_introduce"> </quill-editor>
            <el-button type="primary" class="addBtn">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>

<script>
//局部引入富文本编辑器组件
import { quillEditor } from 'vue-quill-editor'

import 'quill/dist/quill.core.css' // import styles
import 'quill/dist/quill.snow.css' // for snow theme
import 'quill/dist/quill.bubble.css' // for bubble theme

export default {
  data() {
    return {
      //控制tab栏与步骤条的联系
      activeIndex: '0',
      //商品信息的表单数据
      addForm: {
        goods_name: '',
        goods_cat: [],
        goods_weight: 0,
        goods_number: 0,
        goods_price: 0,
        goods_introduce: ''
      },
      //表单验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ]
      },
      // 级联选择器的配置项
      props: {
        children: 'children',
        label: 'cat_name',
        value: 'cat_id',
        expandTrigger: 'hover'
      },
      //分类数据列表
      selectList: [],
      //many数组数据
      manyData: [],
      //only数组数据
      onlyData: [],
      //请求头数据
      uploadHeader: {
        Authorization: window.sessionStorage.getItem('token')
      },
      //图片路径地址
      previewPath: '',
      //控制图片预览弹出框
      openDialog: false
    }
  },
  mounted() {
    this.getGoodList()
  },
  methods: {
    //级联选择器的值发生改变触发的函数
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    //获取分类列表
    async getGoodList() {
      const res = await this.$http.get(`categories`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取分类列表失败')
      }
      this.selectList = res.data
    },
    //当tab栏切换时判断是否进行分类选择
    changeTabs(activeName, oldActiveName) {
      if (oldActiveName == '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择分类参数')
        return false
      }
    },
    //切换tab栏
    async tabChange() {
      if (this.activeIndex == '1') {
        const res = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: 'many' } }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数列表失败!')
        }
        this.$message.success('获取动态参数成功')
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyData = res.data
      } else if (this.activeIndex == '2') {
        const res = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: 'only' } }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('获取静态属性列表失败!')
        }
        this.onlyData = res.data
      }
    },
    //图片预览时触发的函数
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.openDialog = true
    },
    //图片删除时触发的函数
    handleRemove() {},
    onEditorChange() {}
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  components: {
    quillEditor
  }
}
</script>
<style lang="css" scoped>
.addBtn {
  margin-top: 15px;
}
</style>
