<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!-- 提示信息区域 -->
      <el-row class="bt_pt">
        <el-col>
          <el-alert
            title="注意: 只允许第三级的列表分类设置参数!"
            type="warning"
            show-icon
          >
          </el-alert>
        </el-col>
      </el-row>
      <!-- 级联选择器 -->
      <el-row>
        <el-col>
          <span class="cate_selete">请选择分类: </span>
          <el-cascader
            v-model="selectList"
            :options="cateLists"
            :props="props"
            @change="handleChange"
          >
          </el-cascader>
        </el-col>
      </el-row>

      <!-- tab栏 -->
      <template>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="动态参数" name="many">
            <!-- 动态参数内容区域 -->
            <!-- 添加参数按钮 -->
            <el-button
              type="primary"
              size="mini"
              :disabled="isBooean"
              @click="addManyData"
              >添加参数
            </el-button>

            <!-- 参数修改区域 -->
            <template>
              <el-table stripe style="width: 100%" border :data="manyLists">
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                      v-for="(item, i) in scope.row.attr_vals"
                      :key="i"
                      closable
                      @close="handleClose(i, scope.row)"
                      >{{ item }}
                    </el-tag>
                    <!-- 动态添加tag -->
                    <el-input
                      class="input-new-tag"
                      v-if="scope.row.inputVisible"
                      v-model="scope.row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(scope.row)"
                      @blur="handleInputConfirm(scope.row)"
                    >
                    </el-input>
                    <el-button
                      v-else
                      class="button-new-tag"
                      size="small"
                      @click="showInput(scope.row)"
                      >+ New Tag</el-button
                    >
                  </template>
                </el-table-column>

                <el-table-column type="index" label="序列" width="80">
                </el-table-column>
                <el-table-column label="参数名称" prop="attr_name">
                </el-table-column>
                <el-table-column label="操作">
                  <!-- 操作按钮插槽 -->
                  <el-button type="primary" size="mini" icon="el-icon-edit">
                    编辑
                  </el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete">
                    删除
                  </el-button>
                </el-table-column>
              </el-table>
            </template>
          </el-tab-pane>

          <el-tab-pane label="静态属性" name="only">
            <el-button
              type="primary"
              size="mini"
              :disabled="isBooean"
              @click="addOnlyData"
            >
              添加属性
            </el-button>
            <!-- 属性修改区域 -->
            <template>
              <el-table stripe style="width: 100%" border :data="onlyLists">
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                      v-for="(item, i) in scope.row.attr_vals"
                      :key="i"
                      closable
                      @close="handleClose(i, scope.row)"
                      >{{ item }}
                    </el-tag>
                    <!-- 动态添加tag -->
                    <el-input
                      class="input-new-tag"
                      v-if="scope.row.inputVisible"
                      v-model="scope.row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(scope.row)"
                      @blur="handleInputConfirm(scope.row)"
                    >
                    </el-input>
                    <el-button
                      v-else
                      class="button-new-tag"
                      size="small"
                      @click="showInput(scope.row)"
                      >+ New Tag</el-button
                    >
                  </template>
                </el-table-column>
                <el-table-column type="index" label="序列" width="80">
                </el-table-column>
                <el-table-column label="属性名称" prop="attr_name">
                </el-table-column>
                <el-table-column label="操作">
                  <!-- 操作按钮插槽 -->
                  <el-button type="primary" size="mini" icon="el-icon-edit">
                    编辑
                  </el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete">
                    删除
                  </el-button>
                </el-table-column>
              </el-table>
            </template>
          </el-tab-pane>
        </el-tabs>
      </template>
    </el-card>
    <!-- 添加参数弹出层 -->
    <el-dialog
      :title="'添加' + titleText"
      :visible.sync="addDialog"
      width="50%"
      @close="resetFormData"
    >
      <!-- 表单区域 -->
      <el-form
        :model="addFormData"
        :rules="rules"
        ref="submitAddFormDataRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addFormData.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="submitAddFormData">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //选中的数据
      selectList: [],
      //所有分类数据
      cateLists: [],
      //级联选择器配置项
      props: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      //tab栏默认显示的内容
      activeName: 'many',
      //动态参数列表
      manyLists: [],
      //静态属性列表
      onlyLists: [],
      //控制添加动态参数/静态属性的弹出层
      addDialog: false,
      //添加动态/静态时表单的数据
      addFormData: {
        attr_name: ''
      },
      //表单验证参数
      rules: {
        attr_name: [
          {
            required: true,
            message: '参数不能为空',
            trigger: 'blur'
          },
          { min: 3, max: 8, message: '长度在 3 到 8 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  mounted() {
    this.getCateList()
  },
  methods: {
    //获取分类列表参数
    async getCateList() {
      const res = await this.$http.get('categories')

      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.cateLists = res.data
    },
    //获取表格参数列表数据
    async getParamsList() {
      //如果双向绑定的选中参数数组中长度不为3,则证明不是选中的三级分类
      if (this.selectList.length !== 3) {
        this.selectList = []
        return
      }
      const res = await this.$http.get(
        `categories/${this.selectId}/attributes`,
        { params: { sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })

      if (this.activeName === 'many') {
        this.manyLists = res.data
      } else {
        this.onlyLists = res.data
        console.log(this.onlyLists)
      }
    },
    //级联选择器中数据发生改变触发的函数
    handleChange() {
      this.getParamsList()
    },
    //点击tab栏切换触发的函数
    handleClick() {
      this.getParamsList()
    },
    //点击添加动态参数
    addManyData() {
      this.addDialog = true
    },
    //点击添加静态属性
    addOnlyData() {
      this.addDialog = true
    },
    // 关闭弹出层情况表单数据
    resetFormData() {
      this.$refs.submitAddFormDataRef.resetFields()
    },
    //提交添加参数
    submitAddFormData() {
      this.$refs.submitAddFormDataRef.validate(async valid => {
        if (!valid) return

        const res = await this.$http.post(
          `categories/${this.selectId}/attributes`,
          {
            attr_name: this.addFormData.attr_name,
            attr_sel: this.activeName
          }
        )

        if (res.meta.status !== 201) {
          return this.$message.error('添加参数失败!')
        }
        this.$message.success('添加参数成功!')
        this.addDialog = false
        this.getParamsList()
      })
    },
    //点击按钮显示添加属性值的input框
    showInput(row) {
      row.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //输入框失去焦点或按下enter键
    async handleInputConfirm(row) {
      //如果内容输入为空格等无效字符则不进行下面操作
      if (row.inputValue.trim().length == 0) {
        row.inputVisible = false
        return
      }

      // 将用户输入的内容保存到数组中,保证数据响应到view层
      row.attr_vals.push(row.inputValue)

      //发送ajax请求,更改服务器端的数据
      const res = await this.$http.put(
        `categories/${this.selectId}
      /attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_vals: row.attr_vals.join(' '),
          attr_sel: this.activeName
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('添加失败!')
      }
      this.$message.success('添加成功')
      row.inputValue = ''
      row.inputVisible = false
    },
    //点击tag删除属性
    async handleClose(i, row) {
      row.attr_vals.splice(i,1)
      //发送ajax请求,更改服务器端的数据
      const res = await this.$http.put(
        `categories/${this.selectId}
      /attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_vals: row.attr_vals.join(' '),
          attr_sel: this.activeName
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败!')
      }
      this.$message.success('删除成功!')
    }
  },
  computed: {
    //当用户没有选定三级参数时禁用操作按钮
    isBooean() {
      if (this.selectList.length !== 3) {
        return true
      }
      return false
    },
    //获取三级参数的id值
    selectId() {
      if (this.selectList.length == 3) {
        return this.selectList[2]
      }
      return null
    },
    titleText() {
      if (this.activeName === 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    }
  }
}
</script>

<style lang="css" scoped>
.bt_pt {
  margin: 15px 0;
}
.cate_selete {
  line-height: 26px;
  font-size: 14px;
}
.el-tabs {
  margin-top: 20px;
}
.el-tag {
  margin: 15px;
}
.input-new-tag {
  width: 100px;
}
</style>
