<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="10">
          <!-- 搜索商品区域 -->
          <el-input
            placeholder="请输入内容"
            v-model="queryInfo.query"
            clearable
            @clear="this.getUsers"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="this.getUsers"
            >
            </el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <!-- 添加商品按钮 -->
          <el-button type="primary" @click="openAddlists">
            添加商品
          </el-button>
        </el-col>
      </el-row>

      <!-- 表格区域 -->

      <el-table :data="goodData" style="width: 100%">
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称"> </el-table-column>
        <el-table-column prop="goods_price" label="商品价格"> </el-table-column>
        <el-table-column prop="goods_number" label="商品数量">
        </el-table-column>
        <el-table-column prop="goods_weight" label="商品重量">
        </el-table-column>
        <el-table-column prop="add_time" label="上架时间">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 商品列表数据
      goodData: [],
      total: 0
    }
  },
  mounted() {
    this.getGoodlist()
  },
  methods: {
    openAddlists() {
      this.$router.push('/goods/add')
    },
    getUsers() {},
    //获取商品列表数据
    async getGoodlist() {
      const res = await this.$http.get('goods', { params: this.queryInfo })

      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.goodData = res.data.goods
      this.total = res.data.total
      this.$message.success('获取商品列表成功')
    }
  }
}
</script>

<style lang="css" scoped></style>
