<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card class="box-card">
      <!-- 添加分类按钮 -->

      <el-button type="primary" @click="openAddlists">添加分类</el-button>

      <!--添加分类弹出层 -->
      <el-dialog
        title="添加分类"
        :visible.sync="openAddlist"
        width="50%"
        @close="closeAddcateForm"
      >
        <!-- 表单区域 -->
        <el-form
          :model="addCateForm"
          :rules="rules"
          ref="addCateForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="父级分类">
            <!-- 次链选择器 -->
            <el-cascader
              v-model="seleteCate"
              :options="parentCateLists"
              :props="props"
              @change="watchCateChange"
            ></el-cascader>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="openAddlist = false">取 消</el-button>
          <el-button type="primary" @click="submitForm">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 表格区域 -->
      <zk-table
        :data="cateLists"
        :show-index="true"
        :columns="columns"
        :tree-type="true"
        border
        :expand-type="false"
        :selection-type="false"
      >
        <!-- 是否默认 -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            style="color:lightgreen"
            v-if="scope.row.cat_pid === 0"
          ></i>
          <i class="el-icon-error" v-else></i>
        </template>
        <!-- 排序 -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level == 0">等级一</el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level == 1"
            >等级二</el-tag
          >
          <el-tag type="danger" v-else>等级三</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="edit" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="editCate(scope.row.cat_id)"
            >编辑</el-button
          >
          <el-button
            type="danger"
            icon="el-icon-setting"
            size="mini"
            @click="deleteCate(scope.row.cat_id)"
            >删除</el-button
          >
        </template>
      </zk-table>
      <!-- 编辑弹出层 -->
      <el-dialog
        title="修改分类名称"
        :visible.sync="editDialog"
        width="50%"
        @close="editCloseDialog"
      >
        <!-- 编辑表单区域 -->
        <el-form ref="editForm" :model="currentCate" label-width="80px">
          <el-form-item label="活动名称">
            <el-input v-model="currentCate.cat_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialog = false">取 消</el-button>
          <el-button type="primary" @click="submitEdit">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分页区域 -->
      <template>
        <el-pagination
          @size-change="cateSizeChange"
          @current-change="cateCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[3, 5, 7, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </template>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 设置渲染列表数量
      queryInfo: {
        type: 3,
        pagesize: 5,
        pagenum: 1
      },
      total: 0,
      //商品数据列表
      cateLists: [],
      //控制添加分类弹出层
      openAddlist: false,
      //商品分类验证规则
      rules: {
        cat_name: [
          { required: true, message: '请填写商品分类名称', trigger: 'blur' }
        ]
      },
      //选中的分类数据
      seleteCate: [],
      //次连选择器配置项
      props: {
        children: 'children',
        value: 'cat_id',
        label: 'cat_name',
        expandTrigger: 'hover'
      },
      // 添加分类的表单数据对象
      addCateForm: {
        // 将要添加的分类的名称
        cat_name: '',
        // 父级分类的Id
        cat_pid: 0,
        // 分类的等级，默认要添加的是1级分类
        cat_level: 0
      },
      //获取父级分类表
      parentCateLists: [],

      //点击编辑获取当前分类信息
      currentCate: {},
      //控制编辑弹出层的显示与隐藏
      editDialog: false,
      //当前编辑项的id
      cateId: '',

      //表格配置项
      columns: [
        {
          prop: 'cat_name',
          label: '分类名称'
        },
        {
          type: 'template',
          label: '是否默认',
          template: 'isok'
        },
        {
          type: 'template',
          label: '排序',
          template: 'order'
        },
        {
          type: 'template',
          label: '操作',
          template: 'edit'
        }
      ]
    }
  },
  mounted() {
    this.getCateList()
  },
  methods: {
    //获取商品列表数据
    async getCateList() {
      const res = await this.$http.get('categories', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.message.error('获取商品列表失败')
      }
      this.cateLists = res.data.result
      this.total = res.data.total
    },
    //页面显示条目变化监听
    cateSizeChange(val) {
      this.queryInfo.pagesize = val
      this.getCateList()
    },
    //选择跳转的分页监听
    cateCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getCateList()
    },
    //获取所有商品分类
    async getAllCateList() {
      const res = await this.$http.get('categories', { params: { type: 3 } })

      this.parentCateLists = res.data
    },
    //点击弹出添加分类
    openAddlists() {
      this.getAllCateList()
      this.openAddlist = true
    },
    //关闭弹出层清除表单数据
    closeAddcateForm() {
      this.$refs.addCateForm.resetFields()
      this.seleteCate = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    //监听下拉框选项数据的变化,并赋值到addForm中
    watchCateChange() {
      if (this.seleteCate.length > 0) {
        this.addCateForm.cat_pid = this.seleteCate[this.seleteCate.length - 1]
        this.addCateForm.cat_level = this.seleteCate.length

        return
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    //点击确定按钮提交表单数据,验证表单数据
    submitForm() {
      this.$refs.addCateForm.validate(async valid => {
        if (!valid) return
        const res = await this.$http.post('categories', this.addCateForm)

        if (res.meta.status !== 201) {
          return this.$message.error('添加分类失败')
        }
        this.$message.success('添加分类成功!')
        this.getCateList()
        this.openAddlist = false
      })
    },
    //点击编辑弹出dialog并渲染当前数据
    async editCate(id) {
      this.editDialog = true
      const res = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取当前商品分类失败')
      }
      this.currentCate = res.data
      this.cateId = res.data.cat_id
    },
    //关闭dialog时清空表单数据
    editCloseDialog() {
      this.$refs.editForm.resetFields()
    },
    //提交分类信息修改功能
    submitEdit() {
      this.$refs.editForm.validate(async valid => {
        if (!valid) return
        const res = await this.$http.put(
          'categories/' + this.cateId,
          this.currentCate
        )
        if (res.meta.status !== 200) {
          return this.$message.error('修改失败!')
        }
        this.$message.success('修改成功!')
        this.getCateList()
        this.editDialog = false
      })
    },
    //点击删除商品分类功能
    deleteCate(id) {
      this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$http.delete('categories/' + id)
          if (res.meta.status !== 200) {
            return this.$message.error('删除失败')
          }
          this.$message.success('删除成功!')
          this.getCateList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    }
  }
}
</script>

<style lang="css" scoped></style>
